export default function sokolov() {
    const uidField = document.getElementById('uid');
    const phoneField = document.getElementById('phone');
    const maskuid = {
        mask: '00000000',
    };
    const maskphone = {
        mask: '+{7} (000) 000-00-00',
    };
    let uidMask = IMask(uidField, maskuid);
    let phoneMask = IMask(phoneField, maskphone);

    new ClipboardJS('.code-copy');
    const codeSubmit = document.getElementById('code-submit');
    const modal = document.getElementById('modal');
    const modalOpenButton = document.getElementById('modal-open');
    const modalCloseButton = document.getElementById('modal-close');
    const modalTitle = document.getElementById('modal-title');
    const modalText = document.getElementById('modal-text');
    const modalError = document.getElementById('modal-error');
    const modalCode = document.getElementById('modal-code');
    const modalCodeValue = document.getElementById('code-value');

    modalOpenButton.onclick = () => {modalOpen('text')}
    codeSubmit.onclick = () => {requestCode(); return false;}

    const requestCode = () => {
        let uid = uidMask.unmaskedValue;
        let phone = phoneMask.unmaskedValue;
        if(uid.length !== 8 || phone.length !== 11){
            modalOpen('error', 1);
            return false;
        }
        const data = new FormData();
        data.append("uid", uidMask.unmaskedValue);
        data.append("phone", phoneMask.unmaskedValue);
        fetch('ajax/promocodecheck/promocodecheck.php',{
            method: 'post',
            body: data
        }).then(res => res.text()).then(res => codeResult(res));
    }
    const codeResult = (res) => {
        if (res === 'wrong'){
            modalOpen('error', 2);
            return false;
        }
        if (res === 'pechalka'){
            modalOpen('error', 3);
            return false;
        }
        modalOpen('code', res);

    }
    window.onclick = event =>{
        if (event.target === modal || event.target === modalCloseButton) {
            modalClose();
        }
    }
    const modalClose = () => {
        modal.style.display = "none";
        document.body.classList.remove('modal-open');
    }
    const resetModal = () => {
        modalCode.style.display = "none";
        modalText.style.display = "none";
        modalError.style.display = "none";
    }

    const modalOpen = (state, code = '') => {
        resetModal();
        modal.style.display = "block";
        document.body.classList.add('modal-open');
        if(state === 'text'){
            modalTitle.innerText = 'ÐšÐ°Ðº Ð½Ð°Ð¹Ñ‚Ð¸ ID';
            modalText.style.display = "block";
        }else if(state === 'code'){
            modalTitle.innerText = 'Ð’Ð°Ñˆ Ð¿Ñ€Ð¾Ð¼Ð¾ÐºÐ¾Ð´';
            modalCodeValue.innerText = code;
            modalCode.style.display = "block";
        }else if(state === 'error'){
            modalTitle.innerText = 'ÐžÑˆÐ¸Ð±ÐºÐ°';
            if(code == 1){
                modalError.innerText = 'ÐŸÑ€Ð¾Ð²ÐµÑ€ÑŒÑ‚Ðµ Ð¿Ñ€Ð°Ð²Ð¸Ð»ÑŒÐ½Ð¾ÑÑ‚ÑŒ Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ Ð¿Ð¾Ð»ÐµÐ¹';
            }
            if(code == 2){
                modalError.innerText = 'Ð£ÐºÐ°Ð·Ð°Ð½Ð½Ñ‹Ðµ ID Ð¸ Ð½Ð¾Ð¼ÐµÑ€ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð° Ð½Ðµ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÑŽÑ‚, Ð¿Ð¾Ð¶Ð°Ð»ÑƒÐ¹ÑÑ‚Ð°, Ð¿Ñ€Ð¾Ð²ÐµÑ€ÑŒÑ‚Ðµ Ð´Ð°Ð½Ð½Ñ‹Ðµ';
            }
            if(code == 3){
                modalTitle.innerText = 'Ð¡Ð¾Ð¶Ð°Ð»ÐµÐµÐ¼...';
                modalError.innerText = 'ÐšÐ°Ð¶ÐµÑ‚ÑÑ, Ð¿Ñ€Ð¾Ð¼Ð¾ÐºÐ¾Ð´Ð¾Ð² Ð±Ð¾Ð»ÑŒÑˆÐµ Ð½ÐµÑ‚ :(';
            }
            modalError.style.display = "block";
        }
    }
}
